import React, { useState, useEffect } from "react";
import { WeekSeparator, FutureSeparator } from "./Separator";
import Event from "./Event";
import { searchMethod, getByIdMethod } from "./vkApi";
import { addWeeks, isAfter, fromUnixTime } from "date-fns";
import "normalize.css";
import "./App.scss";

const crapList = [116828815, 97035114, 101563208];

const App = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [idList, setIdList] = useState([]);
  const [events, setEvents] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      setIsLoading(true);
      try {
        let response = await fetch(searchMethod());
        let result = await response.json();
        setIdList(result.response.items.map(event => event.id));
      } catch (error) {
        console.error(error);
      } finally {
        setIsLoading(false);
      }
    };
    fetchData();
  }, []);

  useEffect(() => {
    const fetchData = async () => {
      setIsLoading(true);
      try {
        const idListWithoutCrap = idList.filter(id => !crapList.includes(id));
        let response = await fetch(getByIdMethod(idListWithoutCrap.join()));
        let result = await response.json();
        const events = result.response.sort(
          (event, anotherEvent) => event.start_date - anotherEvent.start_date
        );
        setEvents(events);
        console.log(result.response);
      } catch (error) {
        console.error(error);
      } finally {
        setIsLoading(false);
      }
    };

    idList.length && fetchData();
  }, [idList]);

  const isEventSoon = date => {
    const currentDate = new Date();
    const nextWeek = addWeeks(currentDate, 1);
    return !isAfter(fromUnixTime(date), nextWeek);
  };

  return (
    <>
      <div className="title">СОБЫТИЯ</div>
      <div className="container">
        <WeekSeparator />
        {events.map((event, index) => (
          <div key={event.id}>
            <Event
              key={event.id}
              title={event.name}
              link={event.screen_name}
              image={event.photo_200}
              date={event.activity}
              status={event.status}
              isSoon={isEventSoon(event.start_date)}
            />
            {events[index + 1] &&
            isEventSoon(events[index + 1].start_date) !==
              isEventSoon(event.start_date) ? (
              <FutureSeparator />
            ) : (
              ""
            )}
          </div>
        ))}
      </div>
    </>
  );
};

export default App;
