import React from "react";
import "./App.scss";

const Event = ({ image, title, date, status, isSoon, link }) => {
  const soonStatus = isSoon ? "--soon" : "--not-soon";
  return (
    <a href={`https://vk.com/${link}`} className={"event-container event-container" + soonStatus}>
      <div className="image-column">
        <img className="event-image" src={image} alt='logo'/>
      </div>
      <div className="info-column">
        <div className="event-date">{date}</div>
        <div className="event-name">{title}</div>
        <div className="event-status">{status}</div>
      </div>
    </a>
  );
};

export default Event;
